@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@400;600&display=swap);
*{margin:0;padding:0;box-sizing:border-box}body{background-color:#1e1a1a;font-family:'Roboto Mono'}img{width:100%;height:100%}.container{max-width:1230px;margin:0 auto;padding:0 15px}.hide{display:none}.title{text-align:center;font-size:2.0rem;margin:0 auto;font-weight:600;color:bisque}.films{display:flex;position:relative;flex-wrap:wrap;margin-top:90px;margin-left:-30px}.films__card{position:relative;flex:0 1 270px;cursor:pointer;color:#ffffff;margin-left:36px;margin-bottom:80px;overflow-x:hidden}.films__card::before{content:"";position:absolute;opacity:0;width:100%;height:330px;background-color:rgba(126,77,2,0.27);transition:all 0.3s ease;pointer-events:none}.films__card::after{content:"";position:absolute;top:330px;width:0%;height:2px;background-color:#f3cc14;transition:width 0.4s ease-in-out}.films__card:hover.films__card::before{opacity:1}.films__card:hover.films__card::after{width:100%}.films__link{color:#31d05c;width:100px;height:100px}.films__picture{width:100%;height:330px}.films__name{font-size:1.6rem;margin-top:11px;font-weight:600}.films__tags{font-size:0.9rem;white-space:nowrap;overflow:hidden;text-overflow:ellipsis;color:#524141}.films__rating{display:flex;justify-content:center;align-items:center;position:absolute;font-weight:600;top:10px;left:10px;width:35px;height:35px;border-radius:50%;font-size:0.9rem;background-color:#332a2a}

.Header{height:800px;background:url("https://i.playground.ru/p/nqkBjn4OkNy_-tSEhpYhVA.jpeg") no-repeat center/cover;position:relative;z-index:2}.Header::before{content:"";position:absolute;height:100%;width:100%;background-color:rgba(0,0,0,0.58);z-index:-1}.Header__logo{font-size:2.7rem;color:#f3cc14;font-weight:600;display:inline-block;text-decoration:none;padding-top:80px}.navbar{position:absolute;bottom:80px}.navbar__menu{display:flex;list-style:none}.navbar__link{font-size:1.5rem;color:#ffffff;text-decoration:none;margin-left:150px;transition:color .3s ease;position:relative}.navbar__link:hover{color:#f3cc14}.navbar__link::before{content:"";position:absolute;bottom:-4px;display:inline-block;left:0;width:0%;height:2px;background-color:#f3cc14;transition:width .5s ease-in-out}.navbar__link:hover.navbar__link::before{width:100%}

.Content{position:relative}.Content::before{content:"";position:absolute;top:0;height:5px;width:100%;background:linear-gradient(to top, #1e1a1a, black);z-index:1000}.Content__fire-icon{color:#f3cc14}.Content__search{width:300px;margin:0 auto;padding-top:100px;color:#ffffff;font-size:1rem;position:relative}.Content__search input{width:100%;border-radius:20px;padding:4px 22px 4px 25px;background-color:#332a2a;font-weight:600;outline:none;border:none}.Content__search-icon{position:absolute;left:4px;bottom:4px}.Content__search input:focus{background-color:#423636}.Content__close-icon{position:absolute;color:bisque;font-size:1rem;opacity:0.8;right:6px;cursor:pointer;bottom:4px}.Content__close-icon:hover{opacity:1}.pageCount{display:flex;margin-top:100px;margin-bottom:90px}.pageCount__btn{display:flex;justify-content:center;align-items:center;margin-left:17px;background-color:#3c9898;width:40px;height:40px;text-decoration:none;font-size:1.2rem;color:#ffffff}.pageCount__btn:first-child{margin-left:0}.pageCount__btn:hover{background-color:#43bebe}.pageCount__btn--active{background-color:#31d05c}.pageCount__btn--active:hover{background-color:#31d05c}

.Loader{width:120px;height:120px;margin:100px auto;border-radius:50%;border:3px dashed teal;animation:rotate 1s infinite linear}@keyframes rotate{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

.Popular{position:relative}.Popular::before{content:"";position:absolute;top:0;height:5px;width:100%;background:linear-gradient(to top, #1e1a1a, black);z-index:1000}.Popular__title{padding-top:100px}.Popular__cup-icon{color:#e7550c}

.Film{height:150vh;position:relative}.Film::before{content:"";position:absolute;display:inline-block;width:100%;height:100%;left:0;top:0;background-color:rgba(49,48,41,0.6);z-index:-1}.Film__display{display:flex;padding-top:110px}.Film__poster{position:absolute;height:100%;z-index:-2}.Film__picture{width:460px;height:660px}.Film__information{list-style:none;margin-left:20px}.Film__item{color:#f3cc14;width:760px;font-size:1.5rem;border-top-left-radius:10px;background-color:rgba(38,33,33,0.24);margin-bottom:22px;padding:10px 20px;border:3px solid #2f5ebd;overflow-x:hidden;white-space:nowrap;text-overflow:ellipsis;transition:background-color .1s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Film__item:hover{background-color:#587dc0}

.About{color:bisque;position:relative}.About__title{padding-top:90px;margin-bottom:100px}.About__icon-heart{color:#f3294d}.About__advantages{margin:0 auto;max-width:440px;list-style:none;font-size:1.1rem;line-height:29px}.About__description{padding-top:60px;padding-bottom:100px;color:#a9b2b2;line-height:21px;text-align:center;max-width:500px;margin:0 auto}

